<template>
  <div>
    <div class="flex justify-between items-center mb-4">
      <h1 class="text-3xl text-white">Applicant List</h1>
      <div class="">
        <router-link to="#" class="btn btn-lg btn-primary mr-4">
          Export CSV
        </router-link>
        <router-link to="#" class="btn btn-lg btn-primary mr-4">
          Export CSV (All)
        </router-link>
        <router-link to="/employee/import" class="btn btn-lg btn-primary mr-4">
          Import CSV
        </router-link>
        <router-link to="/employee/add" class="btn btn-lg btn-primary">
          Add Applicant
        </router-link>
      </div>
    </div>

    <div class="flex flex-col">
      <div class="-my-2 py-2 overflow-x-auto sm:-mx-6 sm:px-6 lg:-mx-8 lg:px-8">
        <div class="align-middle inline-block rounded min-w-full shadow overflow-hidden border-b border-gray-200">

          <div class="border-b border-gray-300 text-center bg-white pt-4 shadow rounded-t">
            <router-link to="#" class="text-gray-700 border-b-2 border-teal-600 inline-block py-2 px-6">
              Unprocessed
            </router-link>
            <router-link to="#" class="text-gray-700 border-b-0 border-teal-600 inline-block py-2 px-6">
              Scheduled to Psycho Test
            </router-link>
            <router-link to="#" class="text-gray-700 border-b-0 border-teal-600 inline-block py-2 px-6">
              Scheduled to Interview
            </router-link>
          </div>

          <table class="min-w-full">
            <tbody class="bg-white">
            <tr>
              <td colspan="4" class="p-4">
                <div class="flex">
                  <input v-model="search" type="text" class="textbox mr-4" placeholder="Type to search">
                  <button class="btn btn-primary btn-lg">Search</button>
                </div>
              </td>
            </tr>
            <tr v-for="(employee, index) in filteredList" :key="employee.id" class="hover:bg-gray-100">
              <td class="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
                <div class="flex items-center">
                  <div class="flex-shrink-0 h-10 w-10">
                    <router-link :to="`/employee/${employee.id}/edit`">
                      <img class="h-10 w-10 rounded-full" :src="employee.avatar" alt="" />
                    </router-link>
                  </div>
                  <div class="ml-4">
                    <router-link :to="`/employee/${employee.id}/edit`">
                      <div class="text-sm leading-5 font-medium text-gray-900">{{ employee.name }}</div>
                    </router-link>

                    <div class="text-sm leading-5 text-gray-500">{{ employee.email }}</div>
                    <div class="text-sm leading-5 text-gray-500">{{ employee.phone }}</div>
                  </div>
                </div>
              </td>
              <td class="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
                <div class="text-sm leading-5 text-gray-900">{{ employee.position }}</div>
                <div class="text-sm leading-5 text-gray-500">{{ employee.department }}</div>
              </td>
              <td class="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
              </td>
              <td class="px-6 py-4 whitespace-no-wrap text-right border-b border-gray-200 text-sm leading-5 font-medium">
                <router-link :to="`/employee/${employee.id}/edit`" class="text-gray-600 hover:text-indigo-900 focus:outline-none focus:underline">
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-chevron-right"><polyline points="9 18 15 12 9 6"></polyline></svg>
                </router-link>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'Applicant',

    components: {},

    data: () => ({
      search: '',
    }),

    computed: {
      ...mapGetters('employee', [
        'employeeList',
      ]),

      filteredList() {
        return this.employeeList.filter(
          applicant => applicant.name.includes(this.search)
            || applicant.email.includes(this.search)
            || applicant.phone.includes(this.search)
            || applicant.position.includes(this.search)
        )
      },
    },

    methods: {}
  }
</script>
